.footer {
    background: #202020;
    padding-top: 35px;
    padding-bottom: 45px;
    @media (max-width: $md){
        padding-top: 16px;
        padding-bottom: 30px;
    }
    &__top {
        position: relative;
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        padding-bottom: 40px;
        column-gap: 70px;
        &::after {
            opacity: 0.2;
        }
        @media (max-width: 1088px) {
            display: flex;
            justify-content: space-between;
        }
        @media (max-width: $md){
            padding-bottom: 16px;
        }
    }

    &__items {
        display: flex;
        flex-direction: column;
        white-space: nowrap;
        @media (max-width: 1088px) {
            display: none;
        }
        &--visibly{
            @media (max-width: 1088px) {
                display: block;
            }
        }
    }

    &__title {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 19px;
        color: white;
        &:hover {
            text-decoration: underline;
        }
    }

    &__link {
        color: rgba(white, 0.7);
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-top: 10px;
        &:hover {
            text-decoration: underline;
        }
    }
    &__burger {
        display: none;
        @media (max-width: 1088px) {
            display: block;
            display: flex;
            align-items: center;
        }
    }
    &__burger-text {
        color: white;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 19px;
        margin-left: 20px;
    }
    &__contact {
        display: none;

        @media (max-width: 1088px) {
            display: block;
            display: flex;
            align-items: center;
            white-space: nowrap;
        }
        @media (max-width: $md){
            display: none;
        }
    }
    &__contact-num,
    &__contact-mail {
        display: flex;
        align-items: center;
    }
    &__contact-num-text,
    &__contact-mail-text {
        color: $white;
        font-family: "Roboto";
        font-size: 18px;
        font-weight: 700;
        line-height: 23px;
        margin-left: 13px;
    }
    &__contact-num {
        margin-right: 33px;
    }
    &__bot {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        @media (max-width: $md){
            flex-direction: column;
            justify-content: flex-start;
            margin-top: 20px;
        }
    }

    &__bot-left {
        display: flex;
        gap: 60px;
    }

    &__bot-right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        @media (max-width: $md){
            margin-top: 30px;
            align-items: flex-start;
        }
    }
    &__bot-title {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        color: white;
        margin-bottom: 7px;
    }
    &__bot-link {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        color: rgba(white, 0.7);
    }
    &__bot-right-link {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        color: white;
        text-decoration: underline;
    }

    &__bot-img {
        margin-top: 20px;
        height: 22px;
    }
}
