.advantages {
    margin-top: 80px;
    padding-bottom: 55px;
    position: relative;
    @media (max-width: $lg){
        margin-top: 32px;
        padding-bottom: 32px;
    }
    &__title {
        font-size: 44px;
        text-align: center;
        margin-bottom: 44px;
        @media (max-width: $md){
            font-size: 22px;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
    }

    &__left {
        position: relative;
        @media (max-width: 1080px){
            display: none;
        }
    }

    &__img {
        // &--left {
        // }
        &--right {
            position: absolute;
            top: 160px;
            left: 58px;
            border: 10px solid #fff;
        }
    }

    &__text {
        max-width: 701px;
        font-family: Roboto;
        font-size: 18px;
        font-weight: 400;
        line-height: 25px;
        color: $main;
        & + & {
            margin-top: 10px;
        }
        @media (max-width: 1080px){
            max-width: 100%;
            font-size: 16px;
        }
    }
}
