.tour-content {
    .container {
        position: relative;
    }

    &__inner {
        padding-top: 110px;
        padding-bottom: 70px;
        display: flex;
        align-items: flex-start;
        gap: 32px;
    }

    &__menu {
        display: block;
    }
    &__title {
        font-size: 44px;
        line-height: 58px;
        margin-bottom: 50px;
    }

    &__img {
        border-radius: 6px;
    }
}
