.burger {
    --burger-width: 32px;
    --burger-height: 24px;
    --burger-line-height: 4px;
    display: none;
    @include burger;
    &--footer {
        @media (max-width: 1088px) {
            display: block;
        }
    }
    &__menu {
        position: fixed;
        padding-bottom: 100px;
        top: 100%;
        left: 0;
        z-index: 1000;
        width: 100%;
        height: 100%;
        background-color: #fff;
        overflow-y: scroll;
        transition: top 0.3s linear;
        &--active {
            top: 75px;
        }
    }
    &__menu-close {
        position: absolute;
        top: 17px;
        right: 15px;
        z-index: inherit;
    }
    &__menu-ul {
        padding: 0 15px;
        position: relative;
        padding-bottom: 10px;
    }

    &__menu-item {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
    }

    &__menu-title {
        color: black;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 38px;
    }

    &__menu-link {
        color: $blue-text;
        text-decoration: underline;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 38px;
    }
    &__menu-bot {
        margin-top: 20px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        gap: 20px;
        padding: 0 15px;
    }

    &__menu-num,
    &__menu-mail {
        display: flex;
        align-items: center;
    }
    &__menu-num-text,
    &__menu-mail-text {
        color: #000;
        font-family: "Roboto";
        font-size: 18px;
        font-weight: 700;
        line-height: 23px;
        margin-left: 13px;
    }
    &__menu-num {
        margin-right: 33px;
    }
    &__menu-mail {
        margin-right: 45px;
    }
    &__menu-btn {
        width: 100%;
    }
    @media (max-width: $md) {
        display: block;
    }
}
