.map {
    margin-top: 70px;
    @media (max-width: $lg){
        margin-top: 32px;
    }
    &__title {
        text-align: center;
        margin-bottom: 30px;    
    }

    &__map {
        width: 100%;
        height: 609px;
        @media (max-width: $lg){
            height: 500px;
        }
        @media (max-width: $md){
            height: 360px;
        }
    }
}
