.rooms {
    &__block-title {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 43px;
        h2 {
            margin: 0;
        }
    }
    &__items {
        background: $bg-light;
        display: grid;
        grid-template-columns: 1fr 274px;
        column-gap: 32px;
        margin-bottom: 30px;
    }

    &__items-left {
        padding: 23px 0 23px 23px;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
    }

    &__title {
        font-size: 22px;
        font-weight: 700;
        line-height: 26px;
        color: black;
        margin-bottom: 21px;
    }

    &__description {
        margin-bottom: 12px;
        span {
            font-weight: 700;
        }
    }

    &__btn {
        background: transparent;
        border: none;
        text-decoration: underline;
        color: $text-help;
        padding: 0;
        display: flex;
        align-items: center;
        gap: 8px;
    }

    &__items-right {
        padding: 23px 23px 23px 0;
    }

    &__price {
        float: right;
        font-family: Roboto;
        font-size: 18px;
        font-weight: 500;
        line-height: 21px;
        color: black;
        margin-bottom: 17px;
    }

    &__img {
        width: 100%;
        height: auto;
    }
    &__dropdown-png{
        &--active{
            transform: rotate(180deg);
        }
    }
    &__dropdown {
        position: relative;
        z-index: 1;
        display: none;
        padding: 23px 23px 23px 23px;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        &--active {
            display: block;
        }
    }
}
