.base-goriachi-kluch-content {
    .container {
        position: relative;
    }
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 32px;
        padding-top: 110px;
    }
    &__title {
        font-family: PT Serif;
        font-size: 44px;
        font-weight: 700;
        line-height: 58px;
        color: black;
        margin-bottom: 60px;
    }
    &__img{
        width: 100%;
        height: auto;
        margin-bottom: 32px;
    }
}
