.title {
    font-family: PT Serif;
    font-size: 30px;
    font-weight: 700;
    line-height: 39px;
    color: black;

    @media (max-width: 735px) {
        font-size: 22px;
        line-height: 28px;
    }
}
.title-page {
    font-family: PT Serif;
    font-size: 44px;
    font-weight: 700;
    line-height: 58px;
    margin-bottom: 50px;
}
.page-img {
    border-radius: 6px;
    margin-bottom: 32px;
}
.h1 {
    font-family: Roboto;
    font-size: 44px;
    font-weight: 700;
    line-height: 52px;
    color: black;

    margin-top: 80px;
    margin-bottom: 64px;

    @media (max-width: 735px) {
        font-size: 29px;
        line-height: 36px;
    }
}
.h2 {
    font-family: Roboto;
    font-size: 30px;
    font-weight: 700;
    line-height: 39px;
    color: black;

    margin-top: 48px;
    margin-bottom: 28px;

    @media (max-width: 735px) {
        font-size: 22px;
        line-height: 28px;
    }
}
.h3 {
    font-family: Roboto;
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    color: black;

    margin-top: 40px;
    margin-bottom: 28px;

    @media (max-width: 735px) {
        font-size: 19px;
        line-height: 24px;
    }
}
.h4 {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    color: black;

    margin-top: 40px;
    margin-bottom: 28px;

    @media (max-width: 735px) {
        font-size: 16px;
        line-height: 20px;
    }
    &--no-margin-bot {
        margin-bottom: 3px;
    }
}
.p {
    color: $main;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;

    margin: 28px 0;
    span {
        font-weight: 700;
    }
    &--no-margin {
        margin: 0;
    }
    &--limited {
        max-width: 832px;
    }
}
.ol {
    list-style-type: decimal;
}
.ul {
    list-style-type: disc;
}
.li {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    color: black;
    margin-left: 20px;
    & + & {
        margin-top: 10px;
    }
}
.photo-style-1 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 32px;
    margin: 32px 0;
}
.photo-style-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 32px;
    row-gap: 32px;
    margin: 32px 0;
}
.photo-style-3 {
    display: flex;
    gap: 32px;
    margin: 32px 0;
}
.table {
    width: 100%;
}
.tr {
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 21px;
    &--top {
        th {
            background: $bg-light;
            font-weight: 500;
        }
    }
    th {
        border: 1px solid $divid;
        padding: 21px;
    }
}
.inner{
    margin-bottom: 80px;
    padding-top: 110px;
}