.menu {
    border-radius: 6px;
    background: $bg-light;
    min-width: 350px;
    max-width: 350px;
    padding: 30px 25px 25px 25px;
    &__item {
        & + & {
            margin-top: 15px;
        }
    }

    &__title {
        color: $text-help;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 23px;
        margin-bottom: 20px;
    }

    &__link {
        color: $blue-text;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        &:hover {
            text-decoration: underline;
        }
        &--active {
            color: $gren-elem;
            &:hover {
                color: $gren-elem;
                text-decoration: none;
            }
        }
    }
}
