.way {
    background-color: rgba($green, 0.07);
    padding: 50px 0;
    @media (max-width: $lg){
        padding-bottom: 20px;
    }
    &__title {
        text-align: center;
        margin-bottom: 50px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 35px;
        row-gap: 35px;
        @media (max-width: $lg){
            grid-template-columns: repeat(2,1fr );
        }
        @media (max-width: $sm){
            grid-template-columns: repeat(1,1fr );
            text-align: center;
        }
    }
    &__img {
        margin-bottom: 15px;
    }

    &__text {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        color: $main;
    }
}