.info {
    margin-top: 40px;
    margin-bottom: 80px;
    @media (max-width: $md){
        margin: 25px 0;
    }
    &__list {
        display: flex;
        justify-content: space-between;
        gap: 37px;
        @media (max-width: $md){
            display: grid;
            grid-template-columns: repeat(2,1fr );
            gap: 20px;
        }
    }

    &__items {
        max-width: 279px;
    }

    &__text {
        font-family: PT Serif;
        font-size: 58px;
        font-weight: 700;
        line-height: 75px;
        color: $gren-elem;
        white-space: nowrap;
        @media (max-width: 1020px){
            font-size: 44px;
            line-height: 57px;
        }
    }

    &__undertext {
        color: black;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        
        @media (max-width: 1020px){
            font-size: 14px;
        }
    }
}
