.tour-choice {
    $tour-choice: &;
    margin-top: 60px;
    margin-bottom: 80px;
    &__title {
        text-align: center;
        margin-bottom: 50px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 32px;
        row-gap: 32px;
    }

    &__items {
        border: 1px solid $divid;
        border-radius: 6px;
        overflow: hidden;
        &:hover{
            #{$tour-choice}__img{
                transform: scale(1.2)
            }
        }
    }
    &__img {
        width: 100%;
        transition: transform 0.1s linear;
    }

    &__items-title {
        padding-top: 16px;
        padding-left: 15px;
        position: relative;
        z-index: 10;

        color: black;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        background-color: white;
    }

    &__items-price {
        margin-top: 40px;
        margin-bottom: 15px;
        padding-left: 15px;

        color: $main;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
    }
}
