.list-style-one {
    $list-style-one: &;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 32px;
    &__items {
        border-radius: 6px;
        border: 1px solid $divid;
        padding-bottom: 16px;
        overflow: hidden;
        &:hover {
            #{$list-style-one}__img {
                transform: scale(1.2);
            }
        }
    }

    &__img {
        width: 100%;
        transition: transform 0.1s linear;
    }

    &__title {
        position: relative;
        z-index: 10;
        background: white;
        margin-bottom: 30px;
        padding: 0 16px;
        padding-top: 12px;
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        color: black;
    }

    &__price {
        padding: 0 16px;
    }
}
.list-style-two {
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-bottom: 80px;
    &__items {
        display: grid;
        grid-template-columns: 352px 1fr;
        column-gap: 32px;
        border-radius: 6px;
        background-color: $bg-light;
    }

    &__img {
        max-width: 100%;
    }

    &__right {
        padding: 24px 24px 24px 0;
        font-family: Roboto;
        color: black;
    }

    &__title {
        font-size: 24px;
        font-weight: 500;
        line-height: 31px;
    }

    &__text {
        margin-top: 10px;
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        color: $main;
    }

    &__bot {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        margin-top: 35px;
    }

    &__description {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        span {
            font-weight: 700;
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 156px;
        height: 44px;
    }
}


.content-text{
    max-width: 826px;
}