.trans-btn {
    background-color: transparent;
    border: none;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 22px;
    color: white;

    transition: all 0.2s linear;
    &:hover {
        background-color: white;
        color: black;
    }
}
.blue-btn {
    background: $blue-elem;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 30px;
    width: 300px;
    height: 60px;
    transition: background 0.1s linear;
    &:hover {
        background: #004395;
    }
}
.blue-border-btn {
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    color: $blue-text;
    background: transparent;
    border: 1px solid $blue-elem;
    cursor: pointer;
    border-radius: 30px;
    width: 300px;
    height: 60px;
    transition: background 0.1s linear, color 0.1s linear, border 0.1s linear;
    &:hover {
        border: 1px solid #004395;
        background: #004395;
        color: white;
    }
}
.green-download-btn {
    font-family: Roboto;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 21px;
    letter-spacing: 0.02em;
    text-align: left;
    color: white;
    background: $gren-elem;
    width: 234px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    img {
        margin-right: 12px;
    }
}
.blue-download-btn {
    display: flex;
    align-items: flex-end;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 500;
    color: $blue-text;
    img {
        margin-right: 12px;
    }
}
