.gallery {
    background: rgba($blue, 0.07);
    padding-top: 50px;
    padding-bottom: 80px;
    @media (max-width: $md) {
        padding-bottom: 37px;
        padding-top: 20px;
    }
    .slick-track {
        display: flex;
        gap: 32px;
    }
    &__inner {
        overflow: hidden;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 50px;
        @media (max-width: $md) {
            margin-bottom: 25px;
        }
    }
    &__link {
        color: $blue-text;
        transition: color 0.1s linear;
        text-decoration: underline;
        margin-right: 55px;

        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        &:hover {
            color: $active-link;
        }
        @media (max-width: $md) {
            display: none;
        }
        &--mobile {
            margin-top: 32px;
            width: 100%;
            text-align: center;
            display: none;
            @media (max-width: $md) {
                display: block;
            }
        }
    }

    &__arrow {
        cursor: pointer;
        padding: 16px 20px 16px 18px;
        border-radius: 50%;
        border: 1px solid $blue-elem;
    }

    &__arrow--left {
        margin-right: 16px;
        padding: 16px 20px 16px 18px;
    }

    &__arrow--right {
        transform: rotate(-180deg);
    }

    &__items {
        display: flex;
        flex-direction: column;
    }

    &__img {
        width: 100%;
        & + & {
            margin-top: 32px;
        }
    }
}
