.path {
    $path : &;
    color: white;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    position: absolute;
    top: 22px;
    &__link {
        color: white;
        font-family: Roboto;
        font-size: 14px;
        font-weight: 500;
        line-height: 16px;
        &:hover{
            text-decoration: underline;
        }
    }

    &--dark{
        color: $text-help;
        #{$path}__link{
            color: $text-help;
        }
    }
}
