.navigation {
    margin: 62px 0;
    $navigation: &;
    @media (max-width: $lg){
        margin: 40px 0;
    }
    @media (max-width: $md){
        margin: 16px 0;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 32px;
        row-gap: 32px;
        @media (max-width: $lg){
            grid-template-columns: repeat(3,1fr );
        }
        @media (max-width: $md){
            grid-template-columns: repeat(2,1fr );
        }
        @media (max-width: $sm){
            grid-template-columns: 1fr;
        }
    }

    &__item {
        border-radius: 6px;
        border: 1px solid $divid;
        background-color: #fff;
        &:hover{
            #{$navigation}__text{
                color: $active-link;
            }
        }
    }
    &__img {
        width: 100%;
        height: auto;
    }

    &__text {
        padding: 17px;
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 19px;
        color: black;
        transition: color 0.1s linear;
    }
}
