.offers {
    $offers: &;
    margin-top: 80px;
    padding-bottom: 50px;
    position: relative;
    @media (max-width: $lg){
        margin-top: 32px;
    }
    &__title {
        margin-bottom: 40px;
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        column-gap: 32px;
        row-gap: 32px;
        @media (max-width: $lg) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: $sm) {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    &__items {
        position: relative;
        width: 100%;
        overflow: hidden;
        &--no-active{
            display: none;
        }
        &:hover {
            #{$offers}__items-img {
                transform: scale(1.2);
            }
        }
        &--big {
            grid-column: 3/-1;
            grid-row: 1/3;
            @media (max-width: $lg) {
                grid-column: 1/-1;
                grid-row: 3/5;
            }
        }
    }
    &__items-name,
    &__items-price {
        position: absolute;
        z-index: 4;
        color: white;
        max-width: 100%;
        padding: 0 15px;
    }
    &__items-name {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 500;
        line-height: 21px;

        top: 15px;
        &--big {
            font-size: 22px;
        }
        &--last {
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            font-size: 22px;
            line-height: 29px;
        }
    }

    &__items-price {
        font-family: Roboto;
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;

        bottom: 15px;
    }

    &__items-img {
        width: 100%;
        transition: transform 0.1s linear;
    }

    &__items-darkning {
        position: absolute;
        z-index: 3;
        top: 0;
        left: 0;
        opacity: 0.7;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, #000000 0%, transparent 48.44%, transparent 76.04%, #000000 100%);
        border-radius: 6px;
        &--last {
            background: rgba(#000, 0.5);
        }
    }
}
